import React, {ReactElement, useContext, useEffect} from 'react';
import {useState} from 'react';
import {useHistory} from 'react-router-dom';
import {ProgressContext} from '../../context/progressContext';
import './resume.css';

// const solutions: any = JSON.stringify(localStorage.getItem('solutionChoice'));

export default function Resume(): ReactElement {
  const history = useHistory();
  const [solutions, setSolutions] = useState(
    JSON.stringify(localStorage.getItem('solutionChoice')),
  );

  const {setProgress} = useContext(ProgressContext);

  useEffect(() => {
    setProgress(23);
  }, []);

  const handleSubmit = () => {
    // garanties -> differences-assurance-banque
    if (solutions.includes('differences-assurance-banque')) {
      history.push('/resume-epargne');
    } else if (solutions.includes('rendement')) {
      history.push('/resume-mi-garantie-mi-rendement');
    }
  };
  return (
    <div className="resume">
      <div className="main_content">
        <div className="resume_text">
          Nous esprons sincrement que grce  ce formulaire vous en savez plus
          sur la stratgie que vous essayez d'adopter pour votre troisime
          pilier.
        </div>

        <div className="resume_text">
          Dans la prochaine tape, vous obtiendrez un rsum des solutions qui
          vous intressent.
        </div>

        <div className="resume_text">
          Vous pourrez modifier vos montants et la rpartition de votre capital,
          jusqu' ce que les montants vous conviennent.
        </div>
      </div>

      <div className="containerButtonPosition">
        <div className="footer_content">
          <button
            onClick={handleSubmit}
            className="next_button button_position">
            Continuer
          </button>
          <div className="empty_space"></div>
        </div>
      </div>
    </div>
  );
}
